.root {
  display: flex;
  flex-direction: column;
  padding-right: 10px;

  .contract-id {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
    word-break: break-all;
    .id {
      font-size: 10px;
    }
  }

  .plan-types {
    .node {
      color: black;
    }

    .benefit-label {
      color: gray;
      .node {
        color: black;
      }
    }

    .selected {
      &:focus {
        background-color: 'red';
      }
    }
  }
}
