@import 'styles/color.scss';

.root {
  display: flex;
  flex-direction: column;
  overflow: hidden;

  .header {
    flex: 1;
    margin-bottom: 20px;

    .groups {
      flex: 1;
      display: flex;
      flex-direction: row;

      .field {
        width: 25%;
        display: flex;
        flex-direction: column;
        .label {
          font-size: 11px;
        }
        .value {
          font-weight: bold;
        }
      }
    }
  }

  .table {
    flex: 1;

    .hidden {
      display: none;
    }

    th {
      padding: 5px;
      font-size: small;

      div {
        div {
          margin-bottom: unset !important;
        }
      }

      .search {
        width: 100%;
        font-size: unset;
        font-weight: normal;
        padding: 5px;
      }
    }

    td {
      vertical-align: unset;
      font-size: small;
      padding: 5px;
    }

    .cell {
      display: flex;
      flex-direction: column;
    }
  }

  .footer {
    flex: 1;
  }
}

.center {
  text-align: center;
  font-style: italic;
}
