@import 'styles/color.scss';

.root {
  display: flex;
  flex-direction: column;

  .wrapper {
    display: flex;
    flex-direction: row;
    margin-top: 10px;

    .left-side {
      .title {
        font-weight: bold;
        margin-bottom: 10px;
      }

      width: 20%;
    }

    .right-side {
      flex: 1;
    }
  }
}

.center {
  text-align: center;
  font-style: italic;
}
